<template>
  <div class="ThirdpartyAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor1">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-codepen"
                  label="單位"
                  v-model="list.unit"
                  :rules="emptyRules"
                  v-if="dialog"
                  autofocus
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-graph"
                  label="供貨類別"
                  v-model="list.department"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-card-account-details"
                  label="姓名"
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-human-male-female"
                  label="性別"
                  v-model="list.sex"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-phone"
                  label="連絡電話1"
                  v-model="list.phone1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-phone"
                  label="連絡電話2"
                  v-model="list.phone2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-phone"
                  label="連絡電話3"
                  v-model="list.phone3"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-fax"
                  label="傳真"
                  v-model="list.fax"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-mail"
                  label="E-mail"
                  v-model="list.email"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  prepend-icon="mdi-note"
                  label="備註"
                  v-model="list.note"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            >確認新增</v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>{{ this.list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增協力廠商資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = { ...this.keyItems };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改協力廠商資料";
      this.titleStyle = "font-weight-black success lighten-2";
      // this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.list.personnel_snkey = this.$store.state.pData.snkey;
        this.list.create_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //執行動作的人,時間,內容
        let postData = {
          personnel_snkey: this.list.personnel_snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        let url =
          "general/add/" + this.$store.state.databaseName + "/thirdparty";
        this.myAxios(url, postData)
          .then((rs) => {
            // console.log(rs.data);
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已新增" });
              // this.$emit("getAllData");
              this.$store.dispatch("getItems",'thirdparty');
            }
          })
          .catch((err) => {
            this.$swal({ icon: "error", text: "資料存取錯誤!!內容:" + err });
          });
      } else {
        this.$swal({ icon: "error", text: "資料輸入不完整!!請再次確認!!" });
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //執行動作的人,時間,內容
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        let url =
          "general/edit/" + this.$store.state.databaseName + "/thirdparty";
        this.myAxios(url, postData).then((rs) => {
          if (rs.state == 1) {
            this.$swal({
              icon: "success",
              text: "已修改",
            });

            // this.$emit("editFinish", this.list);
            this.$store.dispatch("getItems",'thirdparty');
            
          }
        });
      }
    },
  },
};
</script>

<style lang="scss">
