<template>
  <div class="ThirdpartyList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        協力廠商管理
        <v-img src= "@/assets/img/company_roudou_kumiai.png" aspect-ratio="2.5" contain></v-img>
        
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model = "searchKey"
            v-if="auth.thirdparty_search_key"
          ></v-text-field>

          <popupadd ref="editProcess" v-if="auth.thirdparty_add_key"></popupadd>
        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap"  >
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left">單位</th>
                    <th class="text-left">供貨類別</th>
                    <th class="text-left">姓名</th>
                    <th class="text-left">性別</th>
                    <th class="text-left">連絡電話1</th>
                    <th class="text-left">連絡電話2</th>
                    <th class="text-left">連絡電話3</th>
                    <th class="text-left">傳真</th>
                    <th class="text-left">E-mail</th>
                    <th class="text-left">備註</th>
                    <th class="text-left">創建人員</th>
                    <th class="text-left">修改紀錄</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="">
                          </v-btn> 
                        </template>
                        <v-list>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.thirdparty_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.thirdparty_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{ item.unit }}</td>
                    <td>{{ item.department }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.sex }}</td>
                    <td>{{ item.phone1 }}</td>
                    <td>{{ item.phone2 }}</td>
                    <td>{{ item.phone3 }}</td>
                    <td>{{ item.fax }}</td>
                    <td>{{ item.email }}</td>
                    <td>{{ item.note }}</td>
                    <td>{{ item.create_man }}</td>
                    <td>{{ item.edit_man }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>
import popupadd from "@/views/main/ThirdpartyAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      // items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    items(){
      return this.$store.state.thirdpartyItems;
    },
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";

      return keys.reduce(function(prev, element) {
        return prev.filter(item => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    }
  },

  methods: {
    //取得所有資料
    getAllData() {
      this.$store.dispatch("getItems",'thirdparty');
    },
    //修改功能
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //完成修改功能
    // editFinish(item){
    //   this.items.forEach((i,index)=>{
    //       if (item.snkey == i.snkey){
    //         this.items.splice(index,1,item)
    //       }
    //     })
    // },
    //刪除功能
    del(item) {
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";

          var postData = {
            snkey: item.snkey,
            tablename: "thirdparty",
            datalist: JSON.stringify(item),
          };

          this.myAxios(
              "general/delv3/" + this.$store.state.databaseName + "/" + postData.tablename,
              postData
            )
            .then((rs) => {
              // console.log(rs.data);
              if (rs.state == 1) {
                this.$swal({ icon: "success", text: "已刪除!" });
                this.getAllData();
              }
            });
          
        }
      });
      
    },


  },
};
</script>


<style lang="scss">
.ThirdpartyList {
  .theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr > th{
    font-size: 1rem;
  }
}
</style>